import React from "react";
import { Linking, Text } from "react-native";
import { Wrapper, Container, H1, H2, H3, P } from "./style";
import TopMenu from "../../components/TopMenu";
import blueStyle from "../../../Styles/themes/blueStyle";
const colors = blueStyle.colors;

export default function About({ navigation }) {
  function ShowGitHub(link, text) {
    return (
      <Text
        style={{ color: "blue", marginBottom: 10 }}
        onPress={() => Linking.openURL(link)}
      >
        GitHub: {text}
      </Text>
    );
  }

  function ShowLinkedin(link, text) {
    return (
      <Text style={{ color: "blue" }} onPress={() => Linking.openURL(link)}>
        LinkedIn: {text}
      </Text>
    );
  }

  return (
    <Container>
      <TopMenu nav={navigation} hasArrow={true} name="Sobre" />
      <Wrapper>
        <H1>Equipe</H1>

        <H2>Bruno Almeida Vasconcelos</H2>
        <H3>Front-end</H3>
        <P>Estudante de Engenharia de Software</P>
        <P>Estagiário CDG Hub Inatel</P>
        {ShowLinkedin(
          "https://www.linkedin.com/in/brunovollin/",
          "linkedin.com/in/brunovollin/"
        )}
        {ShowGitHub("https://github.com/BrunoVollin", "github.com/BrunoVollin")}

        <H2>Leonardo Sabra Barcia Lang</H2>
        <H3>Back-end</H3>
        <P>Estudante de Engenharia de Software</P>
        {ShowGitHub(
          "https://github.com/leonardosblang",
          "github.com/leonardosblang"
        )}

        <H2>Fabio de Moura Camargo Neto</H2>
        <H3>Database</H3>
        <P>Estudante de Engenharia de Software</P>
        {ShowLinkedin(
          "https://www.linkedin.com/in/bio353/",
          "linkedin.com/in/bio353/"
        )}
        {ShowGitHub("https://github.com/bio353", "github.com/bio353")}

        <H2>Eduardo Naves Sant'Ana Azevedo</H2>
        <H3>Tester</H3>
        <P>Estudante de Engenharia de Software</P>
        <P>Estagiário MIDC Inatel</P>
        {ShowLinkedin(
          "https://www.linkedin.com/in/eduardo-naves-sant-ana-azevedo-9492871a9/",
          "linkedin.com/in/eduardo-naves"
        )}

        <H1>Projeto</H1>
        <P>
          O projeto Inacoin tem como objetivo a criação de uma criptomoeda
          descentralizada, que seja fácil de usar e que seja uma alternativa
          viável ao real. Para isso, criamos uma moeda digital que possui
          diversas camadas de segurança, que protege o usuário e o impede de
          perder seu dinheiro facilmente, como acontece frequentemente com
          outros produtos.
        </P>
        <P>
          Além disso, com a ajuda da interface amigável de um aplicativo chamado
          de Inacoin Wallet, qualquer pessoa minimamente acostumada com o uso de
          um celular pode armazenar Inacoins e efetuar transações financeiras,
          trazendo um grande diferencial ao mercado saturado de criptomoedas.
        </P>
        <P>
          Por fim, com o eventual sucesso e o crescimento de nossa criptomoeda,
          pretendemos expandir seu uso para comprar produtos e até mesmo usá-la
          dentro do campus do Inatel.
        </P>
        <H1>Funcionamento</H1>
        <P>
          As informações do Inacoin são armazenadas na linguagem Solidity, em um
          contrato na blockchain da Ethereum, mais precisadamente na rede
          Ropsten. A partir de uma API, criada com FastAPI e hospedada no
          Heroku, e a biblioteca web3py, é possível interagir com o contrato
          Inacoin e usar o ABI da mesma para realizar transações. Para checar a
          legitimidade destas transferências, o endereço da moeda foi conectado
          ao node da Infura.
        </P>
        <P>
          Utilizando o React Native, framework do Facebook, foi criado o Inacoin
          Wallet, um aplicativo que traz ao usuário toda a tecnologia do projeto
          Inacoin e consome os dados da API por meio da biblioteca Axios,
          entregando uma experiência elegante e amigável. Os credenciais de
          usuário são criptografadas através de criptografia bcrypt em python e
          enviadas para um banco de dados PostgreSQL, também hospedado no
          Heroku. Para resumir, além de uma criptomoeda, o projeto Inacoin traz
          consigo uma digital inteiramente funcional.
        </P>
        <H1>Resultados</H1>
        <P>
          A integração da nossa criptomoeda com uma carteira digital foi bem
          sucedida, tornando seu uso extremamente prático e trazendo assim um
          grande diferencial ao mercado.
        </P>
        <P>
          Através do nosso aplicativo, é possível realizar transações cujo tempo
          varia entre 10 segundos e 2 minutos, o que é, na maioria das vezes,
          mais rápido do que o PIX. Além disso, a segurança do programa também
          apresentou ótimos resultados e a criação de conta na rede Ethereum é
          totalmente funcional.
        </P>
        <P>
          Em resumo, o Inacoin é seguro, rápido e fácil de usar, o que o torna
          interessante tanto para aqueles que buscam ingressar no mercado de
          criptomoedas agora quanto para aqueles que já possuem maior
          familiaridade e experiência.
        </P>
        <H1>Curiosidades</H1>
        <P>
          O valor de mercado de todas as criptomoedas juntas chegou a mais de
          2,41 trilhões de dólares em 2021. Entre 2015 e 2021, o número de
          usuários com uma carteira blockchain subiu de 3 milhões para mais de
          68 milhões. Ou seja, aumentou o número de usuário em mais de 20 vezes
          em apenas 6 anos.
        </P>
        <P>
          O projeto Inacoin acontece na blockchain do Ethereum, similar a outros
          projetos populares como o Chainlink e o Axis Infinity. A linguagem
          Solidity, usada na criação do projeto, foi fortemente inspirada por
          linguagens como C++ e JavaScript.
        </P>
        <H1>Bibliografia</H1>
        <P>
          [1] NAKAMOTO, Satoshi. Bitcoin: A Peer-to-Peer Electronic Cash System.
          Bitcoin.org, 2008. Disponível em: https://bitcoin.org/bitcoin.pdf.
          Acesso em: 18 de ago. de 2021.
        </P>
        <P>
          [2] ETHEREUM. Ethereum, c2021. Ethereum development documentation.
          Disponível em: https://ethereum.org/en/. Acesso em: 19 de ago. de
          2021.
        </P>
        <P>
          [3] FACEBOOK. React Native, c2021. Docs: getting started. Disponível
          em: https://reactnative.dev/docs/getting-started. Acesso em: 19 de
          ago. de 2021.
        </P>
      </Wrapper>
    </Container>
  );
}
